import React, { useState, } from 'react'
import { BookDetails as BookData } from '../cmps/BookData'
import { BookList } from '../cmps/BookList'

export function BookWishListApp() {
    const [onChangeChackbox, setonChangeChackbox] = useState(false);
    return (
        <section className="BookWishListApp main-container">
            <div className="cont flex space-between align-center justify-center">
                <div className="details-cont">
                    <BookData
                        onChangeChackbox={onChangeChackbox}
                        setonChangeChackbox={setonChangeChackbox} />
                </div>
                <div className="book-list-cont flex">
                    <BookList
                        onChangeChackbox={onChangeChackbox}
                        setonChangeChackbox={setonChangeChackbox} />
                </div>
            </div>
        </section>
    )
}