import React, { useEffect, useState, } from 'react'
import { useDispatch } from 'react-redux'
import { removeBook, saveBook } from '../actions/BookActions';
import { bookService } from '../services/book.service'

export const BookDetails = ({ onChangeChackbox, setonChangeChackbox }) => {
    const dispatch = useDispatch()
    const [books, setBooks] = useState(null);
    const [current, setCurrent] = useState(0);
    const [book, setBook] = useState(null);

    useEffect(() => {
        const getBooks = async () => {
            const books = await bookService.getBooks()
            setBooks(books)
            setBook(books[current])
        }
        getBooks()
    }, [current, books])

    useEffect(() => {
        if (books !== null) {
            if (current >= 0 && current <= books.length) {
                setBook(books[current])
            } else {
                setBook(books[0])
                setCurrent(0)
            }
        }
    }, [current]);

    useEffect(() => {
        const checkboxValue = async () => {
            if (book) {
                let booktest = await getBook(book.title)
                booktest === undefined ? setonChangeChackbox(false) : setonChangeChackbox(true)
            }
        }
        checkboxValue()
    }, [book, onChangeChackbox])

    const paging = (num) => {
        setCurrent(current + num)
        setonChangeChackbox('')
    }

    const getBook = async (title) => {
        let book = await bookService.getByName(title)
        return book
    }
    const getBookTitle = (title) => {
        if (title.length > 20) {
            return 'title long'
        }
        return 'title'
    }

    const stars = (num) => {
        let rate = '⭐'
        for (let i = 0; i < Math.round(num) - 1; i++) {
            rate += '⭐'
        }
        return rate
    }

    const isFirst = () => {
        if (current >= 0 && current + 1 < books.length) {
            return true
        } else return false
    }
    const isLast = () => {
        if (current > 0 && current <= books.length) {
            return true
        } else return false
    }

    const handleChange = async (ev, storeBook) => {
        if (ev.target.checked) {
            setonChangeChackbox(true)
            dispatch(saveBook(book))
        } else {
            let bookToRemove = await getBook(storeBook.title)
            if (bookToRemove) {
                setonChangeChackbox(false)
                dispatch(removeBook(bookToRemove._id))
            }
        }
    }
    return (
        <div>
            {book && <section key={book.title} className="book-data flex align-center">
                {isFirst() && <div className="first" onClick={() => paging(1)}>‹</div>}
                <div className="content flex column">
                    <div className="card-head flex space-between">
                        <h2 className={getBookTitle(book.title)}>{book.title}</h2>
                        <input type="checkbox"
                            onChange={(ev) => { handleChange(ev, book) }}
                            checked={onChangeChackbox}
                            value={onChangeChackbox}
                            name='onChangeChackbox'
                            id="isChecked" />
                    </div>
                    <hr />
                    <span className="author">{book.author}</span>
                    <p className="book-details">{book.description}</p>
                    <span>Rating: {stars(book.rating)}</span>
                    <p>price: ${book.price}</p>
                </div>
                {isLast() && <div className="last" onClick={() => paging(-1)}>›</div>}
            </section>}
        </div>
    )
}