
const initialState = {
    wishList: [],
    filterBy: null,
}

export function BookReducer(state = initialState, action) {
    switch (action.type) {
        case 'SET_BOOKS':
            return {
                ...state,
                wishList: action.books
            }
        case 'SET_FILTER':
            return {
                ...state,
                filterBy: action.filterBy
            }
        case 'ADD_BOOK':
            if (state.wishList === null) {
                return { ...state, wishList: [action.book] }
            }
            return {
                ...state,
                wishList: [...state.wishList, action.book]
            }
        case 'UPDATE_BOOK':
            return {
                ...state,
                wishList: state.wishList.map(book => {
                    if (book._id === action.book._id) return action.book;
                    return book;
                })
            }
        case 'REMOVE_BOOK':
            return {
                ...state,
                wishList: state.wishList.filter(book => book._id !== action.bookId)
            }
        default:
            return state;
    }
}