import { bookService } from '../services/book.service.js';

// Dispatchers
const _removeBook = (bookId) => ({ type: 'REMOVE_BOOK', bookId });
const _setBooks = (books) => ({ type: 'SET_BOOKS', books });
const _setFilter = (filterBy) => ({ type: 'SET_FILTER', filterBy });

// THUNK
export function loadBooks(filterBy) {
    return async (dispatch) => {
        const books = await bookService.query(filterBy);
        dispatch(_setBooks(books));
    }
}
export function removeBook(bookId) {
    return async (dispatch) => {
        bookService.remove(bookId)
        dispatch(_removeBook(bookId))
    }
}
export function setFilter(filterBy) {
    return (dispatch) => dispatch(_setFilter(filterBy))
}

export function saveBook(book) {
    return async (dispatch, getState) => {
        const type = book._id ? 'UPDATE_BOOK' : 'ADD_BOOK';
        const savedBook = await bookService.save(book)
        dispatch({ type, book: savedBook })
    }
}
