import React, { useCallback, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { removeBook, loadBooks } from '../actions/BookActions'

export const BookList = ({ setonChangeChackbox }) => {
    const dispatch = useDispatch()
    const { wishList } = useSelector(state => state.bookModule)

    useEffect(() => {
        dispatch(loadBooks())
    }, [dispatch])

    const onRemove = useCallback((bookId) => {
        setonChangeChackbox(false)
        dispatch(removeBook(bookId))
    }, [])

    return (
        <div>
            <div className="books-wishlist flex column justify-center">
                {wishList && wishList.map((book) => {
                    return <div key={book.title} className="book-list flex space-between">
                        <span><span className="label">&nbsp;</span> {book.title}</span>
                        <span onClick={() => { onRemove(book._id) }}>X</span>
                    </div>
                })}
            </div>
        </div>
    )
}