import { storageService } from './storageService.js'

export const bookService = {
    query,
    save,
    remove,
    getById,
    getEmptyBook,
    getBooks,
    getByName
}

// var gDefaultBooks = [
//     { _id: 'c1', name: 'audu', price: 210, desc: 'c1' },
//     { _id: 'c2', name: 'subali', price: 180, desc: 'c2' },
