// ]

const STORAGE_KEY = 'books'
const gBooks = []
var gDefaultBooks = [
    { _id: 'abc11', title: 'book1', price: 10 },
    { _id: 'abc12', title: 'book2', price: 20 },
    { _id: 'abc13', title: 'book3', price: 30 }
]

async function query(filterBy = undefined) {
    await _loadBooks()
    let booksToReturn = gBooks;
    if (filterBy) {
        var { name, maxPrice, minPrice } = filterBy
        maxPrice = maxPrice || Infinity
        minPrice = minPrice || 0
        booksToReturn = gBooks.filter(book => book.name.toLowerCase().includes(name.toLowerCase())
            && (book.price < maxPrice)
            && book.price > minPrice)
    }
    return Promise.resolve([...booksToReturn]);
}

function getById(id) {
    const book = gBooks.find(book => book._id === id)
    return Promise.resolve(book)
}
function getByName(title) {
    const book = gBooks.find(book => book.title === title)
    return Promise.resolve(book)
}

function remove(id) {
    const idx = gBooks.findIndex(book => book._id === id)
    gBooks.splice(idx, 1)
    storageService.saveToStorage(STORAGE_KEY, gBooks)
    return Promise.resolve()
}

function save(bookToSave) {
    if (bookToSave._id) {
        const idx = gBooks.findIndex(book => book._id === bookToSave._id)
        gBooks.splice(idx, 1, bookToSave)
    } else {
        bookToSave._id = _makeId()
        // bookToSave.imgName = 'default'
        gBooks.push(bookToSave)
    }
    storageService.saveToStorage(STORAGE_KEY, gBooks)
    return Promise.resolve(bookToSave);
}
function getEmptyBook() {
    return {
        name: '',
        price: ''
    }
}
async function _loadBooks() {
    let books = await storageService.loadFromStorage(STORAGE_KEY)
    if (!books || !books.length) books = gDefaultBooks
    storageService.saveToStorage(STORAGE_KEY, books)
    gBooks.push(...books)
}

function _makeId(length = 5) {
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    for (var i = 0; i < length; i++) {
        text += possible.charAt(Math.floor(Math.random() * possible.length));
    }
    return text;
}

async function getBooks() {
    const booksRequest = await fetch('http://s3.amazonaws.com/sundaysky-mock/books/listOfBooks.json')
    const books = await booksRequest.json()
    console.log(books);
    return books
}
