import React from 'react'
import styled from 'styled-components'
import { Link } from 'react-router-dom'
import LogoImg from '../assets/images/home-icon.png'

const MyNav = styled.nav`
display: flex;
align-items: center;
justify-content: space-between;
grid-column: 2/2 ;
gap: 20px;
// background: linear-gradient(
//     0deg,
//     rgba(255, 255, 255, 0) 0%,
//     rgba(255, 255, 255, 0.8) 10%,
//     rgb(97, 126, 226) 40%,
//     rgb(159, 46, 252) 100%
// );
`

const Logo = styled.img`
width: rem(330px);
transform: rotate(350deg);
mixBlendMode: multiply;
width: 30px;
`

const P = styled.p`
    max-width: 44ch;
    margin: 0 0 1em;
`

const MyHeader = styled.header`
    display: grid;
    grid-template-columns: 1fr minmax(240px, 1fr) 1fr;
    // justify-content:space-between;
    // grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
    grid-auto-flow: dense;
    background: linear-gradient(
        0deg,
        rgba(255, 255, 255, 0) 0%,
        rgba(255, 255, 255, 0.8) 10%,
        rgb(97, 126, 226) 40%,
        rgb(159, 46, 252) 100%
    );

`

export const AppHeader = () => {
    return (
        //styld component
        <MyHeader>
            <MyNav>
                <Link to='/'> <Logo src={LogoImg} alt="" /> </Link>
                <P>My Book Wish List</P>
            </MyNav>
        </MyHeader>

        //scss
        // <nav className="main-header header-layout">
        //     <Link to='/'> <img className="logo" src={Logo} alt="" /></Link>
        //     <div className="main-title">My Book Wish List</div>
        // </nav>
    )
}